@import url('https://fonts.googleapis.com/css2?family=MedievalSharp&family=Prompt&display=swap');
@import 'grid.scss';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

$bg1 : linear-gradient(90deg, rgb(255, 255, 255) 0%, rgba(17, 10, 32, 1) 54%, rgba(23, 22, 22, 1) 100%);


html {
  scroll-behavior: smooth;
