body[data-theme='dark'] {
  --colors-text: #ffffff;
  --colors-bg: #080808;
  --colors-section-odd: #050505;
  --colors-section-even: #110f13;

  --colors-bg2: #1a1a1d;
  --colors-bg3: rgba(0, 0, 0, 0.87);
  --colors-bg-grad: linear-gradient(90deg, #080911 0%, #05050e 54%, rgb(5, 0, 14) 100%);
  --colors-bg-half-transp: rgba(0, 0, 0, 0.85);
  --colors-bg-transp-input: rgba(255, 255, 255, 0.1);


  --shadow: #2c2c2c 0 3px 7px;
  --shadow-hover: #5e5e5e 0px 2px 5px;

  --text-shadow-hover: rgb(245, 0, 0) 0px 2px 10px;

  --text-under: #fca0ff;
}

body[data-theme='light'] {
  --colors-text: #000000;
  --colors-bg: #cccccc;
  --colors-section-odd: #ffffff;
  --colors-section-even: rgb(236, 236, 236);

  --colors-bg2: #dddbdf;
  --colors-bg3: rgba(255, 255, 255, 0.87);
  --colors-bg-grad: linear-gradient(90deg, #cdcecf 0%, #cbcbe6 54%, rgb(255, 255, 255) 100%);
  --colors-bg-half-transp: rgba(255, 255, 255, 0.85);
  --colors-bg-transp-input: rgba(0, 0, 0, 0.1);

  --shadow: #686868 0 5px 7px;
  --shadow-hover: #ecebeb 0px 2px 10px;

  --text-shadow-hover: rgb(0, 0, 0) 0px 2px 10px;

  --text-under: #ff8a05;
}

body {
  overflow: auto;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Prompt';
  scroll-behavior: smooth;
  color: var(--colors-text);
  background-color: var(--colors-bg);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.square.entering {
  animation: square 1000ms ease-out forwards;
}

.square.exiting {
  animation: square 501ms ease-in alternate-reverse;
}

.square.exited {
  opacity: 0;
  transform: translateX(-100px);
}

@keyframes square {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }

  50% {
    opacity: 1;
    transform: translateX(30px);
  }

  100% {
    transform: translateX(0);
  }

}

.os-exit {
  transform: translateX(0);
  opacity: 1;
}

.os-exit-active {
  transform: translateX(100px);
  transition: transform 1s, opacity 500ms;
  opacity: 0;
}

.os-exit-done {
  transform: translateX(0);
  opacity: 1;
}

.os-enter {
  opacity: 1;
  transform: translateX(0);
}

.os-enter-active {
  opacity: 0;
  transform: translateX(100px);
  transition: transform 1s, opacity 500ms;
}

.os-enter-done {
  opacity: 1;
  transform: translateX(0);
}



.as-exit {
  transform: translateX(0);
  opacity: 1;
}

.as-exit-active {
  transform: translateX(-100px);
  transition: transform 1s, opacity 500ms;

  opacity: 0;
}

.as-exit-done {
  transform: translateX(0);
  opacity: 1;
}

.as-enter {
  opacity: 1;
  transform: translateX(0);
}

.as-enter-active {
  opacity: 0;
  transform: translateX(-100px);
  transition: transform 1s, opacity 500ms;
}


.es-exit {
  transform: translateY(0);
  opacity: 1;
}

.es-exit-active {
  transform: translateY(100px);
  transition: transform 1s, opacity 500ms;

  opacity: 0;
}

.es-exit-done {
  transform: translateY(0);
  opacity: 1;
}

.es-enter {
  opacity: 1;
  transform: translateY(0);
}

.es-enter-active {
  opacity: 0;
  transform: translateY(100px);
  transition: transform 1s, opacity 500ms;
}






