  // text-transform: uppercase;
  text-align: center;
}

.title-section::after {
  content: "";
  position: absolute;
  bottom: 10px;
  left: 50%;
  height: 2px;
  width: 50px;
  transform: translateX(-50%);
  background-image: radial-gradient(circle farthest-corner at 32.7% 82.7%, rgb(233, 231, 231) 8.3%, rgb(17, 44, 134) 79.4%);
}

.caption {
  position: absolute;
  right: 0;
  bottom: 0;
  padding: 15px;
  min-height: 38px;
  background-color: rgba(255, 255, 255, 0.5);
  color: white;
  z-index: 50;
}

.slide-caption {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: var(--colors-text);

  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
  text-transform: uppercase;
  z-index: 1000;
  background: var(--colors-bg-grad);
  padding: 1rem;
  border-radius: .5rem;
}

.lgbt {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(90deg, #FE0000 16.66%,
      #FD8C00 16.66%, 33.32%,
      #FFE500 33.32%, 49.98%,
      #ebe083 49.98%, 70.64%,
      #7394ce 70.64%, 83.3%,
      #97729e 83.3%);
  background-clip: 'text';
  -webkit-background-clip: text;
}

.slide-caption h3 {
  font-size: 1rem;
  position: relative;
  margin-bottom: 1.5rem;
}

.slide-caption h1 {
  font-size: 1rem;
  text-shadow: 1px 0 5px rgb(12, 12, 190);
}

.slide-caption p {
  text-transform: capitalize;
  font-size: 0.8rem;
}

.slide-caption h3::after {
  position: absolute;
  bottom: -10px;
  left: 0;
  /* transform: translateX(-50%); */
  // background-image: radial-gradient(circle farthest-corner at 312.7% 82.7%, rgb(233, 231, 231) 8.3%, rgb(17, 44, 134) 79.4%);
  background: linear-gradient(90deg,
      #119F0B 16.66%,
      // # 16.66%,
      yellow 16.66%, 33.32%,
      #FD8C00 33.32%, 49.98%,
      #FE0000 49.98%, 66.64%,
      #C22EDC 66.64%, 83.3%,
      #0644B3 83.3%);
  /* background-color: rgb(0, 204, 255); */
  height: 1px;
  width: 100px;
  content: "";
}

@media (min-width: 1200px) {
  .slide-caption h3 {
    font-size: 1.3rem;
  }

  .slide-caption h3::after {
    width: 370px;
  }

  .slide-caption h1 {
    font-size: 5rem;
  }

  .slide-caption p {
    font-size: 1.3rem;
  }
}


